	module.exports = {"data":{"markdownRemark":{"html":"<p>I want to master my consciousness...</p>\n<p>I will be the master of consciousness...</p>\n<p>No... I will live and operate from master consciousness.</p>\n<p>I recognize the ego that wants and chooses based on thoughts and emotions.  I also recognize a part of me\nthat is beyond thought and emotion.  It always can be heard but is seldom listened to...\nThe voice of the heart is what I choose to guide me.  A lot of my ambitions are\nfounded by fear and greed.  I am not very good at them because there is a missing piece of myself\nwhich cannot allow me to thrive in these areas.  I can force myself to do certain things even for some great\ntime; my mind is very strong and persuasive.  However far I get I am always yearning for a path of lesser resistance.\nResistance from the external is natural and to be expected, yet the resistance from within is what keeps me stranded\nand unfulfilled.  I declare my surrender to my heart.  I declare that I will follow my calling which is\nguided not by money, success, fame or any other hollow leisure and only truth, freedom, and authenticity.</p>\n<p>My heart and my soul are the guide for me.  And I will be here as a lighthouse to guide others in their journey,\nif they so choose and wish.  I release myself from the shackles of victim consciousness.  I release myself from the anxiety,\nthe worry, the desire, and the games of cat and mouse with my ego.  I resolve myself from regret, and fear of choosing wrong.\nI can walk with faith along a path projected by my highest self.</p>\n<p>Into the light I will follow; leading my way.</p>\n<p>What this means is taking the hints from my soul when it knows what to do and actually acting out\nthe instructions.  For instance, I just got the hint that it is time to go to bed.  Instead of\nprolonging my bed-time and procrastinating by continued web-surfing, I will take the action to go to bed...\nSo, good night :) </p>","timeToRead":1,"excerpt":"I want to master my consciousness... I will be the master of consciousness... No... I will live and operate from master consciousness. I…","frontmatter":{"title":"Master Consciousness","cover":"https://cdn.pixabay.com/photo/2018/08/09/17/07/soap-bubble-3594979_1280.jpg","date":"September 4th 2018","category":"Earth Star","tags":["Purpose"]},"fields":{"nextTitle":null,"nextSlug":null,"prevTitle":null,"prevSlug":null,"slug":"/master-consciousness"}}},"pathContext":{"slug":"/master-consciousness"}}

